
* {
	margin: 0;
	padding: 0;
	list-style: none;
}
ul li {
	width: 500px;
	height: 50px;
	padding-left: 50px;
	line-height: 50px;
	border: 1px solid brown;
	margin: 10px;
}
ul li button {
	width: 100px;
	float: right;
	line-height: 50px;
	border: 0;
	cursor: pointer;
}
ul li button:nth-of-type(1) {
	background: black;
	color: white;
}
ul li button:nth-of-type(2) {
	background: green;
}
.avtive {
	text-decoration: line-through;
}



